import { useNavigate } from 'react-router-dom';
import { Navigate } from 'react-router';
import {
  deleteMemberState,
  getWorkspacesAsync,
  loginPostAsync,
  loginStateAsync,
  logoutPostAsync,
  signupPostAsync,
} from '@slices/memberSlice';
import { useDispatch, useSelector } from 'react-redux';
import { AppDispatch, RootState } from '../store';
import { deleteWorkspaceState, updateWorkspaceState, updateWorkspaceStateAsync } from '@slices/workspaceSlice';
import { IMember, IWorkspace } from '@typings/db';
import { deleteSettingState, getSettingAsync } from '@slices/settingSlice';
import { deleteGatesState, getGatesAsync } from '@slices/gatesSlice';
import { deletePlaybarState } from '@slices/playbarSlice';
import { useCallback, useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import { getCookie } from '@utils/cookieUtil';

const useCustomMember = () => {
  const navigate = useNavigate();
  const dispatch: AppDispatch = useDispatch();
  const memberState = useSelector((state: RootState) => state.memberSlice);
  const isLogin = !!memberState.email; //---------- 
  const [isInitialLoading, setIsInitialLoading] = useState(true); //    

  //  ->  
  useEffect(() => {
    const initializeMemberState = async () => {
      try {
        const memberInfo = getCookie('member');
        if (memberInfo) {
          memberInfo.email = decodeURIComponent(memberInfo.email);
          memberInfo.password = decodeURIComponent(memberInfo.password);
          await dispatch(loginStateAsync(memberInfo));
          await dispatch(getWorkspacesAsync());
        }
      } catch (error) {
        console.error('Failed to initialize member state:', error);
        toast.error('Failed to load initial data.');
        setIsInitialLoading(false); //  
      } finally {
        setIsInitialLoading(false); //  
      }
    };

    initializeMemberState().then();
  }, [dispatch]);

  const doSignup = async (singupParam: { email: string; nickname: string; password: string }) => {
    const action = await dispatch(signupPostAsync(singupParam));
    const payload = action.payload as IMember;
    if (payload && payload.workspaces && payload.workspaces.length > 0) {
      const workspaceToUpdate = payload.workspaces[0];
      dispatch(updateWorkspaceStateAsync(workspaceToUpdate));
    }
    return payload;
  };

  const doLogin = async (loginParam: { email: string; password: string }) => {
    try {
      const action = await dispatch(loginPostAsync(loginParam));
      if (loginPostAsync.rejected.match(action)) {
        return { success: false, message: action.payload as string };
      }
      const payload = action.payload as IMember;
      if (payload && payload.workspaces && payload.workspaces.length > 0) {
        const workspaceToUpdate = payload.workspaces[0];
        dispatch(updateWorkspaceStateAsync(workspaceToUpdate));
      }
      return { success: true, data: payload };
    } catch (error) {
      const loginError = error as Error;
      console.error('Error during login:', loginError);
      return { success: false, message: loginError.message || 'Login request failed' };
    }
  };

  const doLogout = async () => {
    try {
      await dispatch(logoutPostAsync()).unwrap();
    } catch (error) {
      throw new Error('  .');
    }
  };

  const moveToPath = (path: string) => {
    navigate({ pathname: path }, { replace: true });
  };

  const moveToLogin = () => {
    // navigate({ pathname: '/member/login' }, { replace: true });
    navigate({ pathname: '/member/login' });
  };

  const moveToLoginReturn = () => {
    return <Navigate replace to="/member/login" />;
  };

  const updateSlices = useCallback(
    async (url: string) => {
      try {
        const workspacesAction = await dispatch(getWorkspacesAsync());
        const workspaces = workspacesAction.payload as IWorkspace[];

        if (workspaces && workspaces.length > 0) {
          const selectedWorkspace = workspaces.find((workspace) => workspace.url === url);
          if (selectedWorkspace !== null && selectedWorkspace !== undefined) {
            dispatch(updateWorkspaceState(selectedWorkspace));
          }
          const settingAction = await dispatch(getSettingAsync(url));
          const setting = settingAction.payload;

          if (setting) {
            await dispatch(getGatesAsync(url));
          }
        } else {
          console.error('No workspaces found or workspaces length is less than 1');
        }
      } catch (error) {
        console.error('Failed to update slices after login:', error);
      }
    },
    [dispatch],
  );

  const updateUrlAfterLogin = async () => {
    console.log('updateUrlAfterLogin');
    try {
      let workspaceToUpdateUrl;
      let workspaces = memberState.workspaces;
      if (!Array.isArray(workspaces) || workspaces.length === 0) {
        const workspacesAction = await dispatch(getWorkspacesAsync());
        const workspaces = workspacesAction.payload;
        workspaceToUpdateUrl = workspaces[0].url;
      } else {
        const firstWorkspace = memberState.workspaces[0] as IWorkspace;
        workspaceToUpdateUrl = firstWorkspace.url;
      }
      moveToPath(`/workspace/${workspaceToUpdateUrl}/analyze`);
    } catch (error) {
      // console.error('Failed to member slices after login:', error);
      // toast.dismiss();
      // toast.error('Updating workspace failed due to an unexpected error');
    }
  };

  const deleteSlices = async () => {
    dispatch(deleteMemberState());
    dispatch(deletePlaybarState());
    dispatch(deleteWorkspaceState());
    dispatch(deleteGatesState());
    dispatch(deleteSettingState());
  };

  return {
    memberState,
    isLogin,
    doSignup,
    doLogin,
    doLogout,
    moveToPath,
    moveToLogin,
    moveToLoginReturn,
    updateSlices,
    updateUrlAfterLogin,
    deleteSlices,
    isInitialLoading,
  };
};

export default useCustomMember;
