import {
  postAddRouteAsync,
  postAddWorkspaceMemberAsync,
  postCreateWorkspaceAsync,
  putRouteAsync,
  putRoutesAsync,
  putWorkspaceNameAsync,
  updateRoute,
  updateRoutes,
  updateWorkspaceAsync,
  updateWorkspaceState,
} from '@slices/workspaceSlice';
import { useDispatch, useSelector } from 'react-redux';
import { AppDispatch, RootState } from '../store';
import { IRoute, IWorkspace } from '@typings/db';

const useCustomWorkspace = () => {
  const dispatch: AppDispatch = useDispatch();
  const workspaceState = useSelector((state: RootState) => state.workspaceSlice);

  const postCreateWorkspaceAsyncHook = async (workspaceCreateParam: {
    name: string;
    url: string;
  }): Promise<IWorkspace> => {
    const actionResult = await dispatch(postCreateWorkspaceAsync(workspaceCreateParam)).unwrap();
    await updateWorkspaceStateHook(actionResult);
    return actionResult as IWorkspace;
  };

  const postAddWorkspaceMemberHook = async (addWorkspaceMemberParam: {
    url: string;
    newMember: string;
  }): Promise<IWorkspace> => {
    const actionPayload = await dispatch(postAddWorkspaceMemberAsync(addWorkspaceMemberParam)).unwrap();
    await updateWorkspaceStateHook(actionPayload);
    return actionPayload as IWorkspace;
  };

  const updateWorkspaceStateHook = async (newWorkspace: IWorkspace): Promise<IWorkspace> => {
    const workspaceAction = dispatch(updateWorkspaceState(newWorkspace));
    return workspaceAction.payload;
  };

  const updateWorkspaceAsyncHook = async (newWorkspace: IWorkspace) => {
    try {
      await dispatch(updateWorkspaceAsync(newWorkspace)).unwrap();
    } catch (error) {
      console.error('Error updating workspace:', error);
    }
  };

  const putWorkspaceNameAsyncHook = async (url: string, name: string) => {
    try {
      const workspaceAction = await dispatch(putWorkspaceNameAsync({ url, name })).unwrap();
      return workspaceAction as string;
    } catch (error) {
      console.error('Error updating workspace:', error);
    }
  };

  const postAddRouteAsyncHook = async (url: string, route: IRoute): Promise<IRoute> => {
    const actionPayload = await dispatch(postAddRouteAsync({ url, route })).unwrap();
    return actionPayload as IRoute;
  };

  const putRouteAsyncHook = async (url: string, route: IRoute): Promise<IRoute> => {
    const actionPayload = await dispatch(putRouteAsync({ url, route })).unwrap();
    return actionPayload as IRoute;
  };

  const putRoutesAsyncHook = async (url: string, routes: IRoute[]): Promise<IRoute[]> => {
    const actionPayload = await dispatch(putRoutesAsync({ url, routes })).unwrap();
    return actionPayload as IRoute[];
  };

  function updateRoutesHook(route: IRoute[]) {
    dispatch(updateRoutes(route));
  }

  function updateRouteHook(route: IRoute) {
    dispatch(updateRoute(route));
  }

  return {
    workspaceState,
    postCreateWorkspaceAsyncHook,
    postAddWorkspaceMemberHook,
    updateWorkspaceStateHook,
    updateWorkspaceAsyncHook,
    putWorkspaceNameAsyncHook,
    postAddRouteAsyncHook,
    putRouteAsyncHook,
    putRoutesAsyncHook,
    updateRoutesHook,
    updateRouteHook,
  };
};
export default useCustomWorkspace;
