import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import {
  getWorkspace,
  postAddRoute,
  postAddWorkspaceMember,
  postCreateWorkspace,
  putRoute,
  putRoutes,
  putWorkspaceName,
  updateWorkspace,
} from '@api/workspaceApi';
import { IRoute, IWorkspace } from '@typings/db';

//  :  Workspace 
const initialState: IWorkspace = {
  id: 0,
  name: '',
  url: '',
  ownerId: 0,
  members: [],
  routes: [],
  route: null as unknown as IRoute,
};

export const getWorkspaceAsync = createAsyncThunk('getWorkspacesAsync', async (url: string) => {
  return getWorkspace(url);
});

export const updateWorkspaceStateAsync = createAsyncThunk(
  'updateWorkspaceStateAsync',
  async (workspace: IWorkspace) => {
    return workspace;
  },
);

export const updateWorkspaceAsync = createAsyncThunk('updateWorkspaceAsync', async (workspace: IWorkspace) => {
  return updateWorkspace(workspace);
});

export const postCreateWorkspaceAsync = createAsyncThunk(
  'postCreateWorkspaceAsync',
  async (workspaceCreateParam: { name: string; url: string }) => {
    const updatedWorkspace: IWorkspace = await postCreateWorkspace(workspaceCreateParam);
    return updatedWorkspace;
  },
);

export const putWorkspaceNameAsync = createAsyncThunk(
  'putWorkspaceNameAsync',
  async (putWorkspaceNameParam: { url: string; name: string }) => {
    const updatedWorkspaceName: string = await putWorkspaceName(putWorkspaceNameParam.url, putWorkspaceNameParam.name);
    return updatedWorkspaceName;
  },
);

export const postAddWorkspaceMemberAsync = createAsyncThunk(
  'postAddWorkspaceMemberAsync',
  async (addWorkspaceMemberParam: { url: string; newMember: string }) => {
    return postAddWorkspaceMember(addWorkspaceMemberParam.url, addWorkspaceMemberParam.newMember);
  },
);

export const postAddRouteAsync = createAsyncThunk(
  'postAddRouteAsync',
  async ({ url, route }: { url: string; route: IRoute }) => {
    return postAddRoute(url, route);
  },
);

export const putRouteAsync = createAsyncThunk(
  'putRouteAsync',
  async ({ url, route }: { url: string; route: IRoute }) => {
    return putRoute(url, route);
  },
);

export const putRoutesAsync = createAsyncThunk(
  'putRoutesAsync',
  async ({ url, routes }: { url: string; routes: IRoute[] }) => {
    return putRoutes(url, routes);
  },
);

//  Slice 
const workspaceSlice = createSlice({
  name: 'workspaceSlice',
  initialState: initialState, //   
  reducers: {
    // routes  
    updateRoutes: (state, action: PayloadAction<IRoute[]>) => {
      state.routes = action.payload;
    },
    updateRoute: (state, action: PayloadAction<IRoute>) => {
      state.route = action.payload;
    },
    updateWorkspaceState: (state, action: PayloadAction<IWorkspace>) => {
      state.id = action.payload.id;
      state.name = action.payload.name;
      state.url = action.payload.url;
      state.ownerId = action.payload.ownerId;
      state.members = action.payload.members;
      state.routes = action.payload.routes;
      state.route = action.payload.route;
    },

    deleteWorkspaceState: () => initialState, //    deleteState  
  },
  extraReducers: (builder) => {
    builder
      //    
      .addCase(getWorkspaceAsync.fulfilled, () => {
        console.log('getWorkspaceAsync.fulfilled');
      })
      .addCase(updateWorkspaceStateAsync.fulfilled, (state, action: PayloadAction<IWorkspace>) => {
        console.log('updateWorkspaceStateAsync.fulfilled');
        return action.payload;
      })
      .addCase(updateWorkspaceAsync.fulfilled, (state, action: PayloadAction<IWorkspace>) => {
        console.log('updateWorkspaceAsync.fulfilled');
        return action.payload;
      })
      .addCase(postCreateWorkspaceAsync.fulfilled, (state, action: PayloadAction<IWorkspace>) => {
        console.log('postCreateWorkspaceAsync.fulfilled');
        state.id = action.payload.id;
        state.name = action.payload.name;
        state.url = action.payload.url;
        state.ownerId = action.payload.ownerId;
        state.members = action.payload.members;
        state.routes = action.payload.routes;
        state.route = action.payload.route;
      })
      .addCase(putWorkspaceNameAsync.fulfilled, (state, action: PayloadAction<string>) => {
        console.log('putWorkspaceNameAsync.fulfilled');
        state.name = action.payload;
      })
      .addCase(postAddWorkspaceMemberAsync.fulfilled, () => {
        console.log('postAddWorkspaceMemberAsync.fulfilled');
      })
      .addCase(postAddRouteAsync.fulfilled, (state, action: PayloadAction<IRoute>) => {
        console.log('postAddRouteAsync.fulfilled');
        state.route = action.payload;
      })
      .addCase(putRouteAsync.fulfilled, (state, action: PayloadAction<IRoute>) => {
        console.log('putRouteAsync.fulfilled');
        state.route = action.payload;
      })
      .addCase(putRoutesAsync.fulfilled, (state, action: PayloadAction<IRoute[]>) => {
        console.log('putRoutesAsync.fulfilled');
        state.routes = action.payload;
      })
      .addCase(updateWorkspaceStateAsync.rejected, () => {
        console.log('updateWorkspaceStateAsync.rejected');
      })
      .addCase(updateWorkspaceAsync.rejected, () => {
        console.log('updateWorkspaceAsync.rejected');
      })
      .addCase(getWorkspaceAsync.rejected, () => {
        console.log('getWorkspaceAsync.rejected');
      })
      .addCase(postCreateWorkspaceAsync.rejected, () => {
        console.log('postCreateWorkspaceAsync.rejected');
      })
      .addCase(putWorkspaceNameAsync.rejected, () => {
        console.log('putWorkspaceNameAsync.rejected');
      })
      .addCase(postAddWorkspaceMemberAsync.rejected, () => {
        console.log('postAddWorkspaceMemberAsync.rejected');
      })
      .addCase(postAddRouteAsync.rejected, () => {
        console.log('postAddRouteAsync.rejected');
      })
      .addCase(putRouteAsync.rejected, () => {
        console.log('putRouteAsync.rejected');
      })
      .addCase(putRoutesAsync.rejected, () => {
        console.log('putRoutesAsync.rejected');
      });
  },
});

export const { updateRoutes, updateRoute, deleteWorkspaceState, updateWorkspaceState } = workspaceSlice.actions;

export default workspaceSlice.reducer;
