import { API_SERVER_HOST } from '@api/memberApi';
import jwtAxios from '@utils/jwtUtil';
import { IRoute, IWorkspace } from '@typings/db';

const host = `${API_SERVER_HOST}/api/workspace`;

export const getWorkspace = async (url: string) => {
  const res = await jwtAxios.get(`${host}/${url}`);

  return res.data;
};

export const updateWorkspace = async (workspace: IWorkspace) => {
  const res = await jwtAxios.put(`${host}/`, workspace);

  return res.data;
};

export const postCreateWorkspace = async (workspaceCreateParam: { name: string; url: string }) => {
  const response = await jwtAxios.post(`${host}/`, workspaceCreateParam);

  return response.data;
};

export const putWorkspaceName = async (url: string, name: string) => {
  try {
    const response = await jwtAxios.put(`${host}/${url}/name`, { name });
    return response.data;
  } catch (error) {
    console.error('Error updating workspace name:', error);
  }
};

export const postAddWorkspaceMember = async (url: string, newMember: string) => {
  const res = await jwtAxios.post(`${host}/${url}/members`, null, {
    params: { newMember },
  });
  return res.data;
};

export const postAddRoute = async (url: string, route: IRoute) => {
  const res = await jwtAxios.post(`${host}/${url}/route`, route);

  return res.data;
};

export const putRoute = async (url: string, route: IRoute) => {
  const res = await jwtAxios.put(`${host}/${url}/route`, route);

  return res.data;
};

export const putRoutes = async (url: string, routes: IRoute[]) => {
  const res = await jwtAxios.put(`${host}/${url}/routes`, routes);

  return res.data;
};
