import { Coordinate, IGate, NullableIGate } from '@typings/db';
import { calculateCoordinate } from '@utils/physicsUtil';

function isCoordinateValid(coordinate: NullableIGate['coordinate']): coordinate is Coordinate {
  return coordinate !== null && coordinate.latitude !== null && coordinate.longitude !== null;
}

export function calculateTraveledDistances(gates: NullableIGate[]): NullableIGate[] {
  let accumulatedDistance = 0;

  return gates.map((gate, index) => {
    if (index === 0) {
      return { ...gate, traveledDistance: 0 };
    } else {
      const prevGate = gates[index - 1];
      if (isCoordinateValid(prevGate.coordinate) && isCoordinateValid(gate.coordinate)) {
        const distance = calculateCoordinate(prevGate.coordinate, gate.coordinate);
        accumulatedDistance += distance;
      }
      return { ...gate, traveledDistance: accumulatedDistance };
    }
  });
}

export function sortGatesBySequence(gates: IGate[]): IGate[] {
  return gates.slice().sort((a, b) => a.sequence - b.sequence);
}
